.header {
  display: flex;
  flex-direction: column;
  margin-bottom: 2vh;
}

td {
  cursor: pointer;
}

.expense-buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 2vh 0;
}

.revexp-totals {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2vh;
}

.expenses-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.8519vh;
  margin-top: 1.8519vh;
  background-color: #f9f9f9;
  border: 2px solid #ddd;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.expenses-total h4 {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 500;
  color: #222;
}

.expenses-total span {
  color: #f44336;
  font-weight: bold;
  font-size: 1.5rem;
}

.expenses-total .total-icon {
  font-size: 1.5rem;
  color: #4caf50;
  margin-right: 10px;
}

.field {
  width: 100%; /* Maintain full width */
  box-sizing: border-box; /* Prevent padding overflow */
  padding: 0.5rem; /* Adjust padding for consistent spacing */
}

.label {
  display: flex;
  flex-direction: column;
}

.close-button {
  background: rgba(255, 0, 0, 0.7);
  color: white;
  border: none;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

.add-expense-button {
  background: rgba(255, 255, 0, 0.8);
  color: black;
  border: none;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

.add-expense-button.demo {
  background: red;
  color: white;
}

.filters {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
}

.filters select,
.filters input {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #fff;
  color: #333;
  outline: none;
  transition: border-color 0.3s;
  width: auto;
}

.table-header.exp {
  display: flex;
  justify-content: space-between;
}

.exp-spaced {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.table-container + .table-container {
  margin-top: 2vh;
}

.finance-buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.tabs-container {
  margin-top: 2vh;
}

.tabs-header {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
  border-bottom: 2px solid #ddd;
  padding-bottom: 0.5rem;
}

.tab-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border: none;
  background: none;
  cursor: pointer;
  font-size: 1.1rem;
  font-weight: 500;
  color: #666;
  transition: all 0.3s ease;
  position: relative;
}

.tab-button:hover {
  color: #333;
}

.tab-button.active {
  color: #222;
  font-weight: 600;
}

.tab-button.active::after {
  content: '';
  position: absolute;
  bottom: -0.5rem;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #ffd700;
}

.tab-content {
  margin-top: 1rem;
}
